#boxtext {
  height: 45%;
  padding: 15px;
  width: 450px;
  margin-top: 320px;
  color: #114457;
  font-size: 20px;
}

#boxtitle {
  padding: 30px;
  height: 45%;
  display: flex;
  align-items: flex-start;
  justify-content: flex-end;
}

#title {
  margin-right: 20px;
  text-align: end;
  color: #FFF;
  font-family: "Franklin Gothic Heavy";
  font-size: 10vw;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.Whatsapp1 {
  margin: 5px;
  padding: 10px 20px;
  background-image: url('./funcions/iconos/ic_baseline-whatsapp.svg');
  background-size: 24px 24px;
  background-repeat: no-repeat;
  background-position: center;
}

.correo1 {
  padding: 10px 20px;
  background-image: url('./funcions/iconos/uiw_mail.svg');
  background-size: 24px 24px;
  background-repeat: no-repeat;
  background-position: center;
}

#bootons {
  display: flex;
  flex-direction: column;
  margin-top: 40px;
  align-items: center;

}

button {
  background-color: #ffffff00;
  margin: 1px;
  border: 0px;
  color: white;
}

@media (max-width: 500px) {
  #bootons {
    display: flex;
    column-count: 2;
    column-gap: 25px;
    margin-top: -6px;
    column-count: 2;
    float: right;
  }

  #boxtitle {
    display: block;
  }

  #title {
    font-size: 17vw;
  }

  #boxtext {
    margin-top: 377px;
    width: 80%;
    font-size: 15px;
  }
}