.card {
    background-color: grey;
}

.card {
    margin-top: 25px;
    margin-right: 50px;
    width: 800px;
    height: 500px;
    text-align: center;
    background-color: #ddd;
    flex-shrink: 0;
    border-radius: 50px;
    display: flex;
    flex-wrap: wrap;
    columns: 400px 2;
    column-gap: 25px;
    
}

.boxtextcont {
    height: 400px;
    text-align: center;
    width: 40%;
}

#imagen {
    margin: 10px;
    border-radius: 35px;
    height: 100%;
}

#imagen img {
    width: 100%;
    height: 100%;
    border-radius: 40px;
    object-fit: cover;
}

#contact1 {
    color: white;
    font-size: 20px;
    font-family: 'Arial Narrow Bold', sans-serif;
    width: 150px;
    padding: 15px;
    border-radius: 18px;
    background: #1C6985;
    margin-top: 50px;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
}

.boxtextcont #learn-more {
    background-color: #dddddd00;
    box-shadow: none;
    border: 1px solid #1C6985;
    margin: 10px;
    margin-left: 200px;
}
#more{
    color: #1C6985;
    text-decoration: none;
}
@media (max-width: 500px) {
    .card {
        display: flex;
        flex-direction: column;
        /* Los elementos dentro de .contact se colocarán uno debajo del otro */
        width: 235px;
        height: 558px;
    }

    .boxtextcont #learn-more {

        margin-left: 72px;
    }

    .boxtextcont {
        height: 172px;
        text-align: center;
        width: 87%;
        padding-top: 0px;
        margin-left: 108px;
    }
    #contact1{
        margin-top: 35px;
        font-size: 19px;
        height: 50px;
        text-align: center;
    }
    .title {
        font-size: 22px;
        margin-top: -9px;
        margin-top: -13px;
    }
}