.container-border {
    width: 100%;
    padding: 5px;
    border-radius: 25px;
}

#contact {
    display: flex;
    column-count: 2;
    column-gap: 25px;
    margin-top: 140px
}

#row {
    flex-wrap: wrap;
}

.row {
    width: 100%;
    padding: 10px;
    justify-content: center;
    
    line-height:2rem;
}


.form-control {
    font-family: Arial;
    display: block;
    line-height: 1.5;
    color: rgb(73, 80, 87);
    background-color: rgba(255, 255, 255, 0.258);
    background-clip: padding-box;
    border:0px solid rgb(206, 212, 218);
    border-radius: 0.25rem;
    transition: border-color 0.15s ease-in-out 0s, box-shadow 0.15s ease-in-out 0s;
}

.container-border {
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
}

.form-control .btn .btn-primary {
    margin-top: 5px;
}

.box {
    display: grid;
    align-items: center;
    width: 100%;
   font-family:  Arial;
}


 #send{
    color: rgb(60, 102, 219);    
    background-color: rgb(255, 255, 255);
    width: 50%;
    height: 40px;
    font-size: 15px;
    border-radius: 25px;
    margin-left: 20%;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.42);
    
}

p {
    margin: 20px;
    color: #FFF;
    text-align: center;
    font-family: Inter;
    font-size: 20px;
    font-style: normal;
    font-weight: 200;
    line-height: normal;
}

#whatsapp2{
    width: 50%;
    height: 40px;
    font-size: 20px;
    font-family: Arial, sans-serif;
    color: rgb(60, 102, 219);
    border-radius: 18px;
    background: #FFF;
    border-radius: 25px;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.42);
    display: block;
}
#whatsapp2 a{
    color: rgb(60, 102, 219);
    text-decoration: none;
    display: block;
}
@media (max-width: 500px){
    #contact {
        display: flex;
        flex-direction: column; /* Los elementos dentro de .contact se colocarán uno debajo del otro */
      }
}