h1 {
    text-align: center;
    font-family: "Franklin Gothic Heavy";
    font-size: 50px;
    color: #122830;
    text-shadow: 10px 4px 10px #12283030;
}

.div-wrapper {
    overflow-y: hidden;
    color: black;
    display: flex;
    overflow-x: auto;
    height: 600px;
    margin-top: 125px;

    /* Para Firefox */
    scrollbar-width: none;

    /* Para Internet Explorer 10+ */
    -ms-overflow-style: none;

    /* Para Chrome, Safari y Edge */
    &::-webkit-scrollbar {
        display: none;
    }
}


.div-wrapper button {
    color: black;
}

.title {
    font-size: 30px;
    margin-top: 50px;
}

.boxtextcont {
    margin: 10px;
    padding: 10px;
}

.texto {
    font-size: small;
    margin-top: 10px;
    color: #122830;
    text-align: center;
    font-family: Jost;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

button {
    padding: 10px;
    margin: 5px;
    border-radius: 25px;
}

@media (max-width: 500px) {
    .div-wrapper {
        height: 80vh;
        /* 80% de la altura de la pantalla */
        color: black;
        display: flex;
        overflow-x: auto;
        height: 600px;
        margin-top: -14px;
        scrollbar-width: none;
        -ms-overflow-style: none;
        overflow-y: none;
    }

    .title {
        font-size: 22px;
        margin-top: -9px;
        margin-top: -13px;
    }
    .texto{
        font-size: 11px;
    }
}