#landingPage {
    color: white;
    /* Hacer que landingPage ocupe el total de su contenido */
    display: flex;
    /* Esta línea establece el tipo de display a flex, lo que permite que los elementos hijos se coloquen en una fila o columna. */
    flex-direction: column;
    /* Esta línea establece la dirección de los elementos hijos a columna, lo que significa que se apilan verticalmente. */
    align-items: stretch;
    /* Esta línea hace que los elementos hijos se estiren para ocupar todo el espacio disponible en su contenedor. */
    margin: 0p;
}

#page3 {
    background-image: url(./Components/funcions/iconos/Pge3.jpg);
    background-size: cover;
    background-position: 50% 50%;
}

.Page {
    /* Hacer que cada Page sea del tamaño de la pantalla que lo visualiza */
    flex-grow: 1;
    /* Esta línea permite que el elemento crezca si hay espacio disponible, ocupando todo el espacio extra. */
    height: 100vh;
    /* Esta línea establece la altura del elemento al 100% de la altura de la ventana del navegador. */

    /* Agregar bordes y background para poder ver resultados */
    /* Esta línea agrega un borde sólido de 1px de ancho alrededor del elemento. */
    background-color: #1C6985;
    /* Esta línea establece el color de fondo del elemento a #f0f0f0, que es un color gris claro. */
    padding: 30px;
}

#introPage {
    background-image: url(./Components/funcions/Img/LandingPages.jpg);
    background-size: cover;
    background-position: 50% 50%;
}

#page2 {
    background-image: url(./Components/funcions/iconos/Pge2.jpg);
    background-size: cover;
    background-position: 50% 50%;
    padding: 0px;
}

footer {
    background-color: rgb(18, 18, 18);
    align-items: center;
    padding: 10px;
    display: block;
}

a {
    text-decoration: none;
    color: white;
}
footer{
    align-items: center;
    
    justify-content: center;
}
#contentfooter {
    align-items: center;
    justify-content: center;
    color: rgb(134, 134, 134);
    font-family: Arial, sans-serif;   
    margin-left: 43%;
}

@media (max-width: 500px) {
    .div-wrapper {
        color: black;
        display: flex;
        overflow-x: auto;
        height: 600px;
        margin-top: 43px;
        scrollbar-width: none;
        -ms-overflow-style: none
    }

    .card {
        display: flex;
        flex-direction: column;
        /* Los hijos de .card se colocarán uno debajo del otro */
        height: 100%;
        /* 100% de la altura de .div-wrapper */
        width: 100%;
        /* El ancho de .card será el mismo que el de .div-wrapper */
        padding: 5px;
        /* Reducir el padding */
        box-sizing: border-box;
        /* Asegurarse de que el padding y el borde de .card se incluyan en su ancho total */
        margin: 50px;
        width: 569px;
        height: 542px;
        text-align: center;
        background-color: #ddd;
        flex-shrink: 0;
        border-radius: 50px;
        display: flex;
        flex-wrap: wrap;
        columns: 400px 2;
        column-gap: 25px;
    }

    #contact {
        display: flex;
        flex-direction: column;
        /* Los elementos dentro de .contact se colocarán uno debajo del otro */
    }

    .Page {
        padding: 27px;
    }
}